import React from "react";
import axios from "axios";
import Movie from "../components/Movie";
import "./Home.css";

function getFormatDate(date) {
	var year = date.getFullYear();
	var month = 1 + date.getMonth();
	month = month >= 10 ? month : "0" + month;
	var day = date.getDate();
	day = day >= 10 ? day : "0" + day;
	return year + "" + month + "" + day;
}

class Home extends React.Component {
	state = {
		isLoading: true,
		movies: [],
	};

	getMovies = async () => {
		const serviceKey = "d9b236d72c69aa5461e13d8f4100db4f";
		const today = getFormatDate(new Date()) - 1;

		const {
			data: {
				boxOfficeResult: { dailyBoxOfficeList },
			},
		} = await axios.get("http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json", {
			params: {
				key: serviceKey,
				targetDt: today,
			},
		});
		this.setState({ movies: dailyBoxOfficeList, isLoading: false });
	};

	componentDidMount() {
		this.getMovies();
	}

	render() {
		const { isLoading, movies } = this.state;
		return (
			<section className="container">
				{isLoading ? (
					<div className="loader">
						<span className="loader__text">로딩중......</span>
					</div>
				) : (
					<div className="movies">
						{movies.map((movie) => (
							<Movie key={movie.movieCd} id={movie.movieCd} title={movie.movieNm} openDate={movie.openDt} />
						))}
					</div>
				)}
			</section>
		);
	}
}

export default Home;
