import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({ id, title, openDate }) {
	return (
		<div className="movie">
			<img src="https://kobis.or.kr/kobis/business/mast/mvie/popupImg.do?imgURL=/common/mast/movie/2020/07/f3d5c23f669f4a0b98fa159e52fb5130.jpg" alt={title} />
			<div className="movie__data">
				<h3 className="movie__title">{title}</h3>
				<h1 className="movie__open">{openDate}</h1>
			</div>
		</div>
	);
}

Movie.propTypes = {
	id: PropTypes.number.isRequired,
	openDate: PropTypes.number.isRequired,
	title: PropTypes.string.isRequired,
};

export default Movie;
